/**  */
import React from 'react';
import PropTypes from 'prop-types';
import { Menu, Icon } from 'antd';
import { Link } from 'dva/router';
const SubMenu = Menu.SubMenu;

function getMenuSelectedKey(location) {
  if (!location) return '';
  const { pathname } = location;
  if (!pathname) return '';
  return pathname.replace('/', '');
}

const BearMenus = ({ lightThem, menuMode, siderOpenKeys, topHeaders, onMenuOpenChange, onSwitchTheme, onSwitchMenuMode, location }) => {

  const comProps = {
    theme: lightThem ? "light" : "dark",
    mode: menuMode === 'left' ? 'inline' : 'horizontal',
    onClick: onSwitchMenuMode
  }

  // openKeys
  let menuProps = menuMode === 'left' ? Object.assign(comProps, {
    onOpenChange: onMenuOpenChange,
    openKeys: siderOpenKeys
  }) : Object.assign(comProps, { style: { lineHeight: '64px' } });

  const menukey = getMenuSelectedKey(location);

  return (
    <Menu {...menuProps} selectedKeys={Array.of(menukey)}>
      <SubMenu key="home" title={<span><Icon type="home" /><span></span></span>}>
        <Menu.Item key="dashboard1"><Link to="/dashboard1"> 1</Link></Menu.Item>
        <Menu.Item key="dashboard2"><Link to="/dashboard2"> 2</Link></Menu.Item>
      </SubMenu>

      <Menu.Item key="mail">
        <Link to="/mail"><Icon type="mail" /><span></span></Link>
      </Menu.Item>

      <SubMenu key="mainlayout" title={<span><Icon type="layout" /><span></span></span>}>
        <Menu.Item key="layout"><Link to="/layout"></Link></Menu.Item>
        <Menu.Item key="menu"><Link to="/menu"></Link></Menu.Item>
        <Menu.Item key="card"><Link to="/card"></Link></Menu.Item>
        <Menu.Item key="grid"><Link to="/grid"></Link></Menu.Item>
        <Menu.Item key="gridplayground"><Link to="/gridplayground"></Link></Menu.Item>
      </SubMenu>

      <SubMenu key="general" title={<span><Icon type="filter" /><span></span></span>}>
        <Menu.Item key="icon"><Link to="/icon"></Link></Menu.Item>
        <Menu.Item key="chart" style={{ display: 'none'}}><Link to="/chart"></Link></Menu.Item>
        <SubMenu key="navigation" title={<span><Icon type="tag-o" /></span>}>
          <Menu.Item key="pagination"><Link to="/pagination"></Link></Menu.Item>
          <Menu.Item key="breadcrumb"><Link to="/breadcrumb"></Link></Menu.Item>
          <Menu.Item key="steps"><Link to="/steps"></Link></Menu.Item>
        </SubMenu>
        <SubMenu key="uielem" title={<span><Icon type="tag-o" /></span>}>
          <Menu.Item key="avatar"><Link to="/avatar"></Link></Menu.Item>
          <Menu.Item key="tag"><Link to="/tag"></Link></Menu.Item>
          <Menu.Item key="button"><Link to="/button"></Link></Menu.Item>
          <Menu.Item key="switch"><Link to="/switch"></Link></Menu.Item>
          <Menu.Item key="calendar"><Link to="/calendar"></Link></Menu.Item>
          <Menu.Item key="radio"><Link to="/radio"></Link></Menu.Item>
          <Menu.Item key="checkbox"><Link to="/checkbox"></Link></Menu.Item>
          <Menu.Item key="input"><Link to="/input"></Link></Menu.Item>
          <Menu.Item key="badge"><Link to="/badge"></Link></Menu.Item>
          <Menu.Item key="timeline"><Link to="/timeline"></Link></Menu.Item>
          <Menu.Item key="slider"><Link to="/slider"></Link></Menu.Item>
        </SubMenu>
        <SubMenu key="dataentry" title={<span><Icon type="tag-o" /></span>}>
          <Menu.Item key="mention"><Link to="/mention"></Link></Menu.Item>
          <Menu.Item key="rate"><Link to="/rate"></Link></Menu.Item>
          <Menu.Item key="input" style={{ display: 'none'}}><Link to="/upload"></Link></Menu.Item>
          <Menu.Item key="select"><Link to="/select"></Link></Menu.Item>
          <Menu.Item key="transfer"><Link to="/transfer"></Link></Menu.Item>
          <Menu.Item key="dropdown"><Link to="/dropdown"></Link></Menu.Item>
          <Menu.Item key="autocomplete"><Link to="/autocomplete"></Link></Menu.Item>
          <Menu.Item key="cascader"><Link to="/cascader"></Link></Menu.Item>
          <Menu.Item key="datepicker"><Link to="/datepicker"></Link></Menu.Item>
          <Menu.Item key="timepicker"><Link to="/timepicker"></Link></Menu.Item>
        </SubMenu>
        <SubMenu key="data" title={<span><Icon type="tag-o" /></span>}>
          <Menu.Item key="carousel"><Link to="/carousel"></Link></Menu.Item>
          <Menu.Item key="tabs"><Link to="/tabs"></Link></Menu.Item>
          <Menu.Item key="modal"><Link to="/modal"></Link></Menu.Item>
          <Menu.Item key="treeselect"><Link to="/treeselect"></Link></Menu.Item>
          <Menu.Item key="progress"><Link to="/progress"></Link></Menu.Item>
          <Menu.Item key="tree"><Link to="/tree"></Link></Menu.Item>
          <Menu.Item key="collapse"><Link to="/collapse"></Link></Menu.Item>
          <Menu.Item key="popover"><Link to="/popover"></Link></Menu.Item>
          <Menu.Item key="tooltip"><Link to="/tooltip"></Link></Menu.Item>
          <Menu.Item key="alert"><Link to="/alert"></Link></Menu.Item>
          <Menu.Item key="message"><Link to="/message"></Link></Menu.Item>
          <Menu.Item key="notification"><Link to="/notification"></Link></Menu.Item>
          <Menu.Item key="popconfirm"><Link to="/popconfirm"></Link></Menu.Item>
        </SubMenu>
      </SubMenu>

      <SubMenu key="form" title={<span><Icon type="disconnect" /><span></span></span>}>
        <Menu.Item key="baseform"><Link to="/baseform"></Link></Menu.Item>
        <Menu.Item key="advancedform"><Link to="/advancedform"></Link></Menu.Item>
        <Menu.Item key="advancedsearch"><Link to="/advancedsearch"></Link></Menu.Item>
      </SubMenu>

      <SubMenu key="table" title={<span><Icon type="wallet" /><span></span></span>}>
        <Menu.Item key="basetable"><Link to="/basetable"></Link></Menu.Item>
        <Menu.Item key="advanced"><Link to="/advanced"></Link></Menu.Item>
        <Menu.Item key="crud" style={{ display: 'none'}}><Link to="/crud">CRUD</Link></Menu.Item>
        <Menu.Item key="playground"><Link to="/playground"></Link></Menu.Item>
        <Menu.Item key="custable"><Link to="/custable"></Link></Menu.Item>
      </SubMenu>

      <SubMenu key="page" title={<span><Icon type="appstore-o" /><span></span></span>}>
        <Menu.Item key="blank"><Link to="/blank"></Link></Menu.Item>
        <Menu.Item key="profile"><Link to="/profile"></Link></Menu.Item>
        <Menu.Item key="login"><Link to="/login"></Link></Menu.Item>
        <Menu.Item key="signup"><Link to="/signup"></Link></Menu.Item>
        <Menu.Item key="lock"><Link to="/lock"></Link></Menu.Item>
        <Menu.Item key="notfound"><Link to="/notfound">404</Link></Menu.Item>
      </SubMenu>

      <SubMenu key="motion" title={<span><Icon type="dot-chart" /><span></span></span>}>
        <Menu.Item key="logogather"><Link to="/logogather">Logo</Link></Menu.Item>
        <Menu.Item key="tableanim"><Link to="/tableanim"></Link></Menu.Item>
        <Menu.Item key="listanim"><Link to="/listanim"></Link></Menu.Item>
        <Menu.Item key="detailswitch"><Link to="/detailswitch"></Link></Menu.Item>
        <Menu.Item key="listsort"><Link to="/listsort"></Link></Menu.Item>
        <Menu.Item key="picanim"><Link to="/picanim"></Link></Menu.Item>
        <SubMenu key="sub2" title="">
          <Menu.Item key="banneranim"><Link to="/banneranim">Banner</Link></Menu.Item>
        </SubMenu>
      </SubMenu>

      <Menu.Item key="about">
        <Link to="/about"><Icon type="copyright" /><span></span></Link>
      </Menu.Item>

      {topHeaders && topHeaders.length > 0 && topHeaders.map(v => v)}
    </Menu>
  )
}

BearMenus.propTypes = {
  onMenuOpenChange: PropTypes.func,
  handleSwitchMenuMode: PropTypes.func,
  onSwitchTheme: PropTypes.func,
  lightThem: PropTypes.bool,
  menuMode: PropTypes.string,
  siderOpenKeys: PropTypes.array
}

export default BearMenus;



